import { useState, useEffect } from 'react';

import { getRecentGames } from '../../api/users';

import styles from '../../css/Profile.module.css';

const Profile = (props) => {
    const { wallet, isProfileOpen, setIsProfileOpen, pacesBalance } = props;

    const [ recentGames, setRecentGames ] = useState();

    useEffect(() => {
        getRecentGames(wallet.publicKey.toString()).then(history => {
            setRecentGames(history);
        })
    }, [wallet.publicKey, setRecentGames]);

    return (
        <div className={isProfileOpen ? styles.ProfileBlock : styles.ProfileNone}>
            <div className={styles.ProfileMain}>
                <button onClick={() => setIsProfileOpen(false)}/>
                <div className={styles.ProfileInner}>
                    <div>
                        <p>Wallet ID</p>
                        <p style={{ overflow: "hidden", textOverflow: "ellipsis", maxWidth: "50%" }}>{wallet.publicKey.toString()}</p>
                    </div>
                    <div>
                        <p>Games played:</p>
                        <p>{recentGames?.games ? recentGames.games.length : "0"}</p>
                    </div>
                    <div>
                        <p>Coin balance:</p>
                        <p>{pacesBalance || 0}</p>
                    </div>
                    <div>
                        <p>Contest history:</p>
                        <div className={styles.History}>
                        {
                            recentGames?.games ? (
                                recentGames.games.slice(0,5).map( (gameId, i) => 
                                    gameId.substring(0,8) !== "testtest" ? (
                                        <div key={i} className={styles.HistoryEntry}>
                                            {`${gameId.substring(8) === "deuceswild" ? "2's Wild " 
                                                : (gameId.substring(8) === "4swild" ? "4's Wild" 
                                                : (gameId.substring(8) === "secretwild" ? "Secret Wild" 
                                                : (gameId.substring(8) === "8swild" ? "8's Wild" 
                                                : (gameId.substring(8) === "6swild" ? "6's Wild"
                                                : (gameId.substring(0,8) === "tourney0" ? "February Card Contest Tourney" : "Classic 5 Card")))))}
                                                ${gameId.substring(0,7) !== "tourney" ? String(Number(gameId.substring(2,4)) + 1).padStart(2, '0') : ""}/${gameId.substring(0,2)} - 
                                                Rank ${recentGames.ranks[i]}/${recentGames.totals[i]}`}
                                        </div>
                                    ) : (
                                        <></>
                                    )
                                )
                            ) : (
                                <></>
                            )
                        }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Profile;